.overlay-backdrop{
    position: absolute;
    top: 0%;
    left: 0%;

    width: 100vw;
    height: 100vh;

    z-index: 5;

    background-color: rgba(0, 0, 0, 0.75);

    overflow: hidden;
}

.overlay{

    position: absolute;
    display: flex;
    flex-direction: column;

    top: 10%;
    left: 20%;

    background: #FFFFFF;
    box-shadow: 0px 30px 12px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    
    width: 830px;
    height: 650px;

    z-index: 10;
}

.overlay-clear-button{
    border: none;
    padding: 0;
    margin: 25px;
    background: none;

    width: fit-content;
    align-self: flex-end;
}

.overlay-content{

    /* border: 1px dashed #C1CFDA; */
    height: 650px;
    
    display: flex;
    flex-direction: row;

    display: flex;
    justify-content: space-around;
    row-gap: 10px;
    /* column-gap: 10px; */

    margin: 0;
    margin-right: 35px;
    margin-left: 35px;
    
    overflow-x: hidden;
    overflow-y: scroll;


    /* background: url('../../../Res/Images/border.svg'); */
    background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' stroke='%2320A4F3FF' stroke-width='4' stroke-dasharray='10%2c 10' stroke-dashoffset='0' stroke-linecap='round'/%3e%3c/svg%3e");
    background-repeat: no-repeat;
    

}


.overlay-content-input{
    text-decoration: none;
    border: none;
    background: none;
    width: 100%;
    /* text-align: left; */
    margin: 10px;
    text-align: start;
    font-size: 14px;
    color: #03191E;

    overflow-x: hidden;
    overflow-y: scroll;

}
.overlay-content-input:focus{
    outline: none;
}
.lower-body-text-input::placeholder{
    color: #C1CFDA;
}



.overlay-word-limit{
    margin: 16px;
    margin-right: 35px;
    /* margin-bottom: 25px; */
    font-size: 14px;
    text-align: end;

    color: #03191E;

}

.overlay-end-button{
    margin: 35px;

    display: flex;
    flex-direction: row;

    height: max-content;
    position: relative;

    justify-content: flex-end;
}


.overlay-post-button{
    
    border: none;
    padding: 0;
    margin: 0;

    background: none;

    border: 1px solid #03191E;
    border-radius: 8px;

    width: fit-content;
    height: 40px;

    display: flex;
    align-items: center;    

}
.overlay-post-button-text{

    font-size: 18px;
    line-height: 22px;
    font-style: normal;
    font-weight: normal;

    /* margin-top: 10px;
    margin-bottom: 10px; */
    margin-left: 40px;
    margin-right: 35px;

}

.overlay-post-button-img{
    
    /* margin-top: 10px;
    margin-bottom: 10px; */
    margin-left: 0px;
    margin-right: 16px;

    display: flex;
    width: fit-content;
    height: fit-content;
    
}

.overlay-post-button:hover {
    background: #03191E;
}
.overlay-post-button:hover .overlay-post-button-text{
    color: #FFFFFF;
}
