
/* 
    @className : .stream-list-item-container

    @description
    main item container for the list item 
*/
.stream-list-item-container{
    width: 100%;
    height: min-content;
    list-style: none;

    color: #C1CFDA;
}

/* 
    @className : .stream-list-item && .stream-list-item-count

    @description
    manages the item look and feel
*/
.stream-list-item{
    border: none;
    padding: 0;
    margin: 0;
    text-decoration: none;
    background: none;
    color: #C1CFDA;
    font-size: 48px;
    line-height: 59px;
}
.stream-list-item-count{
    font-size: 9px;
    vertical-align: 25px;

    margin-top: 2px;
}


.stream-list-item-container:hover{
    opacity: 0.5;
}






@media screen and (max-width: 1200px){
    .stream-list-item{
        font-size: 40px;
    }
}