/* 
    @className : .main-holder

    @description
    property addition to card
*/
.main-holder{
    justify-content: space-between;
}








/* 
    @className : .upper-body

    @description
    manages the upper body
*/

.upper-body{
    margin:0;
    margin-top: 15px;
    margin-left: 15px;
}

.upper-body-text{
    margin: 0;
    margin-top: 40px;
    font-size: 48px;
}









/* 
    @className : .upper-body-text-input

    @description
    manages the upper inputs
*/

.upper-body-text-input {
    text-decoration: none;
    border: none;
    background: none;

    width: 95%;
    font-size: 48px;
    font-weight: normal;
    text-transform: uppercase;
    font-family: 'Montserrat';
}
.upper-body-text-input::placeholder{
    color: #F4B860;
    opacity: 0.55;
}
.upper-body-text-input:focus{
    outline: none
}
.upper-body-text-input.invalid::placeholder{
    color: #ED4956;
    opacity: 0.55;
}



/* 
    @className : .upper-body-end

    @description
    manages the upper body end supporter line 
*/
.upper-body-end{
    margin: 0;
    margin-top: 20px;
}









/* 
    @className : .lower-body

    @description
    manages the lower body 
*/
.lower-body{
    display: flex;
    flex-direction: column;
    margin: 0;
    margin-bottom: 30px;
}

.lower-body-text{
    display: flex;
    flex-direction: column;
    align-items: center;

    font-size: 24px;
}




/* 
    @className : .lower-body-text-input

    @description
    manages the lower body inputs 
*/
.lower-body-text-input {
    text-decoration: none;
    border: none;
    background: none;

    /* width: 95%; */
    font-size: 24px;
    font-weight: lighter;
    text-align: center;
    text-transform: uppercase;

}
.lower-body-text-input::placeholder{
    color: #F4B860;
    opacity: 0.55;
}
.lower-body-text-input:focus{
    outline: none;
}
.lower-body-text-input.invalid::placeholder{
    color: #ED4956;
    opacity: 0.55;
}