.list-container{
    list-style: none;
    
    display: flex;
    flex-direction: row;

    background: #FFFFFF;
    box-shadow: 12px 12px 24px rgba(196, 196, 196, 0.5);
    border-radius: 15px 0px;

    margin-bottom: 40px;
    cursor: grab;
}


.list-image{
    width: 50%;
    height: inherit;
    border-radius: 15px 0px 0px 0px;
    
    background-size: contain;
    background-repeat: no-repeat;
}





.list-content{
    width: 50%;

    display: flex;
    flex-direction: column;

    border-top: 2px solid #20A4F3;
}

.list-content-title{
    font-size: 38px;
    font-weight: bolder;
    color: #03191E;

    margin: 15px;
}

.list-content-description{
    font-size: 14px;
    color: #03191E;

    min-height: 200px; /* 198px accurate*/
    display: flex;
    flex-direction: column;
    justify-content: center;


    margin: 15px;
    margin-right: 20px;

    white-space: pre-wrap;

}

.list-content-buttons{
    display: flex;
    flex-direction: row;

    justify-content: flex-end;
    margin: 5px;
    margin-right: 10px;
}

.list-content-buttons-element{
    border: none;
    padding: 0;
    margin: 0;
    text-decoration: none;
    background: none;

    margin-right: 10px;
}