.main-holder{
    justify-content: center;
    align-items: center;
}





.info-card{
    width: max-content;  
    background: #FFFFFF;

    border: 0px solid #FFFFFF;
    border-radius: 20px;

    box-shadow: 0px 6px 12px 4px rgba(0, 0, 0, 0.25);
}

.info-card-header{
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: fit-content;
    align-items: center;
    margin: 20px;
    margin-left: 18px;
    margin-right: 18px;
    font-size: 24px;
}
.info-card-header-text{
    margin-left: 15px;
}
.info-card-header-subtext{
    color: #20A4F3;
}




.info-card-icon-input{
    margin: 26px;
    margin-left: 28px;
    margin-right: 28px;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.info-card-icon-input-button{
    border: none;
    padding: 0;
    margin: 0;
    text-decoration: none;
    background: none;
}





.info-card-stream-input{
    margin-left: 28px;
    margin-right: 28px;
    margin-bottom: 28px;
    position: relative;     /* for absolute positioning of dropdown*/
    display: flex;
    flex-direction: column;
}

.info-card-stream-input-dropdown{
    /* width: 100%; */
    height: 40px;
   
    border: 2px solid #C1CFDA;
    border-radius: 5px;

    color: #C1CFDA;
    font-size: 18px;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    cursor: default;
}
.info-card-stream-dropdown-name{
    margin-left: 10px;
}
.info-card-stream-dropdown-arrow{
    height: 24px;
    margin-right: 10px;
    transition: all 300ms ease-in-out;
    z-index: 1;
}

/* .info-card-stream-input-dropdown:hover .info-card-stream-dropdown-arrow{
    transform: rotate(180deg);
    transform-origin: center center;
} */
.info-card-stream-input-dropdown:hover {
    border: 2px solid #20A4F3;
    color: #03191E;
}

.info-card-stream-dropdown-dialog{
    display: none;
    position: absolute;
    background: #FFFFFF;
    z-index: 2;
    left: 0%;
    right: 0%;
    top: 50px;
}






.info-card-details-button{
    margin-left: 28px;
    margin-right: 28px;
    margin-bottom: 28px;
    position: relative;     /* for absolute positioning of dropdown*/
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.info-card-details-button-preview{
    background: none;
    width: 165px;
    height: 40px;
    border: 1px solid #03191E;
    color: #03191E;
    border-radius: 8px;
    font-size: 18px;
}
.info-card-details-button-preview:hover{
    box-shadow: 4px 4px 8px 0px rgba(0, 0, 0, 0.25);
}


.info-card-details-button-publish{
    background: #03191E;
    width: 165px;
    height: 40px;
    border: 1px solid #03191E;
    color: #FFFFFF;
    border-radius: 8px;
    font-size: 18px;
}

.info-card-details-button-publish:hover{
    box-shadow: 4px 4px 8px 0px rgba(0, 0, 0, 0.25);
}
