
/* 
    @className : .main-holder

    @description
    manages the frame 
*/
.main-holder{
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

/* 
    @className : .sub-holder

    @description
    manages the subframe
*/
.sub-holder{
    display: flex;
    flex-direction: row;
    overflow: hidden;
}

