.dropdown-item{
    list-style: none;
    align-items: flex-start;
    font-size: 18px;
    color: #03191E;
    /* width: 100%; */
    height: 40px;
    margin-left: 10px;
    margin-right: 10px;
    /* border-bottom: 1px solid #C1CFDA; */
    cursor: pointer;
}

.dropdown-item button{
    border: none;
    padding: 0;
    margin: 0;
    text-decoration: none;
    background: none;

    font-size: 18px;
    color: #03191E;
    height: 40px;
    padding-left: 10px;
}

.dropdown-item:hover button{
    color: #20A4F3;

}

.dropdown-item:hover {

    /* border: 1px solid #C1CFDA; */
    border-radius: 20px;
    background: #C1CFDA;

}