.main-holder{
    width: 24.4vw;

    overflow: hidden;

    cursor: default;
}

.nav-right{
    height: 100%;
    
    display: flex;
    flex-direction: column;

    align-items: center;
}





.nav-compose{
    border: none;
    padding: 0;
    margin: 0;
    text-decoration: none;
    background: none;

    width: fit-content;
    height: 40px;

    margin-top: 20px;

    background: #F4B860;
    border-radius: 20px;

    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;

}


.nav-compose-icon{
    margin-left: 20px;
}


.nav-compose-text{
    margin-left: 10px;
    margin-right: 20px;

    font-size: 14px;
    font-weight: 300;
}





.nav-follow{
    border: none;
    padding: 0;
    margin: 0;
    text-decoration: none;
    background: none;

    width: fit-content;
    height: 24px;

    margin-top: 40px;

    background: none;
    /* border-radius: 20px; */

    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
}


.nav-follow-icon{
    /* margin-left: 20px; */
}


.nav-follow-text{
    color: #C1CFDA;

    margin-left: 5px;

    font-size: 14px;
}



.nav-title{
    margin-top: 10px;

    font-size: 36px;
    color: #F4B860;
}

.nav-line-separator{
    margin-top: 20px;
    margin-left: 10px;
    margin-right: 10px;
    width: 95%;

    border: 2px solid rgba(193, 207, 218, 0.25);
    border-radius: 10px;
}






.nav-audi{

    display: flex;
    flex-direction: row;
    align-items: center;

    margin-top: 20px;

}

.nav-audi-icon{
    margin-right: 15px;
}

.nav-audi-text{
    font-size: 18px;
    color: #C1CFDA;
}



.nav-rec-header{
    font-size: 14px;
    color: #C1CFDA;
    align-self: flex-start;

    margin-top: 20px;
    margin-left: 10px;
}


.nav-rec-content{

    align-self: flex-start;
    
    margin-top: 10px;
    margin-left: 10px;

}

.nav-rec-topic{
    border: none;
    padding: 0;
    margin: 0;
    text-decoration: none;
    


    background: #C1CFDA;
    border-radius: 20px;

    font-size: 16px;
    font-weight: lighter;
    color: #061E40;

    width: fit-content;
    min-height: 25px;

    margin-left: 5px;
    margin-right: 5px;
    margin-top: 10px;
    cursor: pointer;

}

.nav-rec-topic-text{
    /* margin-top: 2.5px;
    margin-bottom: 2.5px; */
    vertical-align: middle;
    margin-left: 10px;
    margin-right: 10px;
}