.list-item{
    margin: 0px;
    margin-right: 24px;
    list-style: none;
    color: #03191E;
    font-size: 16px;
    font-weight: bold;
}

.list-item a{
    
    text-decoration: none;
}

.list-item :hover{
    color: #20A4F3;
}

@media screen and (max-width: 1400px){
    .list-item{
        font-size: 12px;
    }
}