
/* 
    @className : .main-holder

    @description
    manges the frame background
*/
.main-holder{
    background: url('../../Res/Images/HomePageDemoImage.svg');
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    /* background-size: 50vh calc(100vh - 50px); */
}


/* 
    @className : .add-button

    @description
    manges the add button
*/
.add-button{
    border: none;
    padding: 0;
    margin: 0;
    text-decoration: none;
    background: none;

    border-radius: 50%;
    width: min-content;
    position: absolute;
    bottom: 20px;
    right: 25px;
}

.add-button:hover{
    border-width: none;
    border-radius: 50%;
    background: #AFAFAF;
    box-shadow: 0px 0px 8px 3px #AEAEAE;
   
    
}