.main-select{
    /* width: 100%; */
    height: 200px; /* basic 40px*/
    background: none;
   
    border: 2px solid #20A4F3;
    border-radius: 5px;

    padding: 0;
    margin: 0;

    overflow-x: hidden;
    overflow-y: scroll;
    scroll-behavior: smooth;
    /* scrollbar-width: none; */
    /* -ms-overflow-style: none; */

    text-decoration: none;

    cursor: default;
}
