
/* 
    @className : .main-holder

    @description
    manages the frame 
*/
.main-holder{
    justify-content: space-between;
}






.header-container{
    display: flex;
    flex-direction: column;
}


/* 
    @className : .header

    @description
    manages the frame header
*/
.header{
    position: relative;
    margin: 0%;

    font-size: 25px;
    font-weight: 600;
    /* left: 25px;
    top: 15px; */

    margin-left: 25px;
    margin-top: 15px;
}

.header p{
    margin: 0%;
}









/* 
    @className : .content-holder

    @description
    manages the content 
*/
.content-holder{
    display: flex;
    flex-direction: column;
    align-self: center;
    margin-top: 50px;
    margin-bottom: 50px;

}

/* 
    @className : .sub-content-holder

    @description
    manages the subcontent
*/
.sub-content-holder{
    display: flex;
    flex-direction: row;
}





/* 
    @className : .main-subtitle

    @description
    manages the frame subtitle
*/
.main-subtitle{
    /* position: absolute; */
    margin:0%;

    /* left:106px; */
        /* top:162px; */
    /* top:228px; */
}

.stream-icon{
    vertical-align: middle;
    margin-left: 2px;
}




/* 
    @className : .stream-container

    @description
    manages the scrollable div
*/
.stream-container{
    display: flex;
    /* position: absolute; */
    /* left: 101px; */
        /* top: 186px; */
    /* top:252px; */
    width: 458px;
    height: 319px;
    overflow-x: hidden;
    overflow-y: scroll;

    scroll-behavior: smooth;
    scrollbar-width: none;
    -ms-overflow-style: none;
}
.stream-container::-webkit-scrollbar{
    display: none;
}
/* 
    @className : .stream-container-scroll

    @description
    manages the arrow-keys
*/
.stream-container-scroll{
    display: flex;
    height: 319px;
    /* position: absolute; */
    flex-direction: column;
    justify-content: space-between;
        /* top: 186px; 66px due to 64px nav bar*/
    /* top:252px; */
    /* left: 555px; */
}
/* 
    @className : .main-holder

    @description
    manages the scroll button 
*/
.stream-container-scroll-button{
    border: none;
    padding: 0;
    margin: 0;
    text-decoration: none;
    background: none;
}
.stream-container-scroll-button:hover{
    opacity: 0.5;
}




.footer{
    /* position: absolute; */
    display: flex;
    flex-direction: row;
    /* align-items: flex-start; */

    /* align-self: flex-start; */
    /* bottom: 0px; */
    justify-content: space-between;
    font-size: 12px;
    margin-bottom: 5px;
    margin-left: 5px;

    cursor: default;
}

.footer-text{
    display: flex;
    flex-direction: column;
    align-items: center;

    margin-left: 30px;
}











@media screen and (max-width: 1200px){
    .stream-container{
        width: 369px;
    }
}