.overlay-backdrop{
    position: absolute;
    top: 0%;
    left: 0%;

    width: 100vw;
    height: 100vh;

    z-index: 5;

    background-color: rgba(0, 0, 0, 0.75);
    overflow: hidden;

}

.overlay{

    position: absolute;
    display: flex;
    flex-direction: column;

    top: 25%;
    left: 33%;

    background: #FFFFFF;
    box-shadow: 0px 30px 12px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    
    width: 495px;
    height: 450px;

    z-index: 10;

    cursor: default;
}

.overlay-clear-button{
    border: none;
    padding: 0;
    margin: 25px;
    background: none;

    width: fit-content;
    align-self: flex-end;
}

.overlay-content{

    /* border: 1px dashed #C1CFDA; */
    height: 309px;
    
    display: flex;
    flex-direction: row;

    display: grid;
    grid-template-columns: auto auto auto;
    justify-content: space-around;
    row-gap: 10px;
    /* column-gap: 10px; */

    margin: 0;
    margin-right: 35px;
    margin-left: 35px;
    margin-bottom: 30px;
    
    overflow-x: hidden;
    overflow-y: scroll;


    /* background: url('../../../Res/Images/border.svg'); */
    background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' stroke='%23C1CFDAFF' stroke-width='4' stroke-dasharray='10%2c 10' stroke-dashoffset='0' stroke-linecap='round'/%3e%3c/svg%3e");
    background-repeat: no-repeat;
    
    cursor: pointer;
}

.overlay-content-images{
    
    background-size: contain;
    background-repeat: no-repeat;

    border: none;
    padding: 0;
    margin: 0;

    margin-top: 6px;

    width: 130px;
    height: 130px;

}



.overlay-description{
    text-align: center;
    margin: 10px;
    margin-bottom: 25px;
    font-size: 18px;
    color: #03191E;
}