.outer-circle{
    transition: all 300ms ease-in-out;
}
.back-rect{
    transform: rotate(-20deg);
    transform-origin: 40.8711px 52.415px;
    transition: all 300ms ease-in-out;
}


.front-rect{
    transform: rotate(-5deg);
    transform-origin: 47.2188px 50.1604px;
    transition: all 300ms ease-in-out;
}
.mountain-path{
    /* transform: rotate(-5deg);
    transform-origin: 47.2188px 50.1604px; */
    transition: all 300ms ease-in-out;
}
.sun{
    transform: rotate(-5deg);
    transform-origin: 51.4444px 55.4371px;
    transition: all 300ms ease-in-out;
}







.svg-holder:hover .outer-circle{
    stroke: #20A4F3;
}
.svg-holder:hover .back-rect{
    fill: #20A4F3;
    transform: rotate(-30deg) translate(-4.4238px,1.909px);
    transform-origin: 36.4473px 54.324px;
}
.svg-holder:hover .front-rect{
    fill: #20A4F3;
    transform: rotate(20deg) translate(14.1962px, -0.2888px);
    transform-origin: 61.415px 49.8716px;
}
.svg-holder:hover .mountain-path{
    transform: rotate(20deg) translate(14.1962px, -0.2888px);
    transform-origin: 61.415px 49.8716px;
}
.svg-holder:hover .sun{
    transform: rotate(20deg) translate(11.5768px, 1.0027px);
    transform-origin: 63.0142px 56.4398px;
}
